import React from "react";
import "./BoxEmpty.scss"
export function BoxEmpty() {
  return (
    <>
    <div className="box">
          <h2 className="box__title">Заказов нет</h2>
          <p className="box__description">
            Вероятней всего, вы не заказывали ещё нечего
            Для того, чтобы заказать, перейди на главную страницу.
          </p>
          <img src="/img/empty-box.png" alt="Empty cart" className="box__img" height="300"  decoding="async"/>
    </div>
    </>
    )
}