import "./searhPage.scss";
import React from "react";
import { BasketEmpty } from "../components/BasketEmpty/BasketEmpty";
import { useState, useEffect } from "react";
import { BoxEmpty } from "../components/box/boxEmpty/BoxEmpty";
import { BoxFull } from "../components/box/BoxFull/BoxFull";
export function Order({ setMasOrder,masOrder}) {
        return (
        <>
            <div>
                <div className='container'>
                    {masOrder.length ==0?<BoxEmpty/>:<BoxFull masOrder={masOrder} setMasOrder={setMasOrder}/>}
                </div>
            </div>
        </>

    )
}