import React from "react";
import "./modalOrder.scss"
import { useState } from "react";
export function ModalOrder({open,setOpen,number}){
  return (
    <div className="modal"onClick={() => setOpen(!open)}>
    <div className="modal__body">
      <button className="modal__close_white" onClick={() => setOpen(!open)}>✕</button>
      <p className="modal_msg">Заказ принят</p><br />
      <p className="modal_msg">Ваш заказ: {number}</p>
      <button className="modal_btn" onClick={() => setOpen(!open)}>OK</button>
    </div>
  </div>
  )
}