import React from "react";
import { BasketEmpty } from "../components/BasketEmpty/BasketEmpty";
import { useState } from "react";
import { BasketFuul } from "../components/BasketEmpty/BasketFuul";
import { Link } from "react-router-dom";
import { ModalOrder} from "../components/modalOrder/modalOrder";
export function Basket({productBys,setCountMenu,countMenu,sum,setSum,setProductsBuy,masOrder, setMasOrder}){
  let [open, setOpen] = useState(false);
  let [number, setNumber] = useState();
    return(
        <>
    <div className="basket">
    <div className="container container--cart">
    {productBys==false?<BasketEmpty open={open} setOpen={setOpen}/>:<BasketFuul setNumber={setNumber} masOrder={masOrder} setMasOrder={setMasOrder} number={number} open={open} setOpen={setOpen} productBys={productBys} setProductsBuy={setProductsBuy}setSum={setSum} setCountMenu={setCountMenu} countMenu={countMenu} sum={sum} />}
    </div>
    </div>
    {open &&
        (
          <ModalOrder open={open} setOpen={setOpen} number={number}/>
        )}
        </>
    )
}