import "./searhPage.scss";
import React from "react";
import { useState, useEffect } from "react";
import { Search } from "../components/header/Search/Search";
import { Product } from "../components/product/product";
export function SearchPage({ search, setSerch }) {
    let [ProductData, SetProducts] = useState([]);
    let ProductItems = search == "" ? [] : ProductData.filter(item => item.title.toLowerCase().includes(search.toLowerCase())).map(item => <Product {...item} key={item.index} />)
    useEffect(() => {
        fetch(`https://6397cc9586d04c76339de5d3.mockapi.io/products?`)
            .then(res => res.json())
            .then(json => {
                SetProducts(json)
            })
    }, [search])
    return (
        <>
            <div>
                <div className='container'>
                    <div className='search__item'>
                        <Search search={search} setSerch={setSerch} />
                    </div>
                    {
                        !ProductItems.length == 0 &&
                        (
                            <div className="products" >
                                {ProductItems}
                            </div>
                        )
                    }
                </div>
            </div>
        </>

    )
}